<template><section>

<h1>下拉菜单 Dropdown</h1>
<h2>基本用法</h2>
<p>悬浮出现在页面顶部，显示全局的通知提醒消息。</p>
<demo-block>
                            <div class="source" slot="source">
  <as-row :offset="20">
    <as-col :col="3">
      <as-dropdown>
        <a class="droplabel">
          Dropdown hover
        </a>
        <as-dropdown-menu slot="list">
          <as-dropdown-item command="a">黄金糕</as-dropdown-item>
          <as-dropdown-item command="b"><as-icon name="goods"></as-icon>狮子头</as-dropdown-item>
          <as-dropdown-item command="c"><as-icon name="edit"></as-icon>螺蛳粉</as-dropdown-item>
          <as-dropdown-item command="d" disabled><as-icon name="edit"></as-icon>双皮奶</as-dropdown-item>
          <as-dropdown-item command="e" divided=""><i class="as-icon icon-like"></i>蚵仔煎</as-dropdown-item>
        </as-dropdown-menu>
      
    </as-dropdown></as-col>
    <as-col :col="3">
      <as-dropdown full-width="">
        <a class="droplabel">
          Dropdown Full Width
        </a>
        <as-dropdown-menu slot="list">
          <as-dropdown-item command="a">黄金糕</as-dropdown-item>
          <as-dropdown-item command="b"><as-icon name="goods"></as-icon>狮子头</as-dropdown-item>
          <as-dropdown-item command="c"><as-icon name="edit"></as-icon>螺蛳粉</as-dropdown-item>
          <as-dropdown-item command="d" disabled><as-icon name="edit"></as-icon>双皮奶</as-dropdown-item>
          <as-dropdown-item command="e" divided=""><i class="as-icon icon-like"></i>蚵仔煎</as-dropdown-item>
        </as-dropdown-menu>
      
    </as-dropdown></as-col>
  </as-row>

</div>
