<h2>指令事件</h2>
<p>点击菜单项后会触发事件，用户可以通过相应的菜单项 key 进行不同的操作</p>
<demo-block>
                            <div class="source" slot="source">
  <as-row :offset="20">
    <as-col :col="3">
      <as-dropdown @command="handleCommand">
        <a class="droplabel">
          Dropdown hover
        </a>
        <as-dropdown-menu slot="list">
          <as-dropdown-item command="a">黄金糕</as-dropdown-item>
          <as-dropdown-item command="b"><as-icon name="goods"></as-icon>狮子头</as-dropdown-item>
          <as-dropdown-item command="c"><as-icon name="edit"></as-icon>螺蛳粉</as-dropdown-item>
          <as-dropdown-item command="d" disabled>双皮奶<as-icon name="time" slot="append"></as-icon></as-dropdown-item>
          <as-dropdown-item command="e" divided="">蚵仔煎<as-icon name="fire" slot="append"></as-icon></as-dropdown-item>
        </as-dropdown-menu>
      
    </as-dropdown></as-col>
    <as-col :col="3">
      <as-dropdown trigger="click" @command="handleCommand">
        <a class="droplabel">
          Dropdown click
        </a>
        <as-dropdown-menu slot="list">
          <as-dropdown-item command="a">黄金糕</as-dropdown-item>
          <as-dropdown-item command="b"><as-icon name="goods"></as-icon>狮子头</as-dropdown-item>
          <as-dropdown-item command="c"><as-icon name="edit"></as-icon>螺蛳粉</as-dropdown-item>
          <as-dropdown-item command="d" disabled>双皮奶<as-icon name="time" slot="append"></as-icon></as-dropdown-item>
          <as-dropdown-item command="e" divided="">蚵仔煎<as-icon name="fire" slot="append"></as-icon></as-dropdown-item>
        </as-dropdown-menu>
      
    </as-dropdown></as-col>
  </as-row>

</div>
